.button-up {
    width: var(--maxuiux-spacing-48);
    height: var(--maxuiux-spacing-64);
    background-color: var(--maxuiux-color-white-rgba-060);
    backdrop-filter: var(--maxuiux-blur-medium);
    border-radius: var(--maxuiux-border-radius-small);
    position: fixed;
    right: var(--maxuiux-spacing-44);
    bottom: var(--maxuiux-spacing-128);
    z-index: var(--maxuiux-z-index-90);
    cursor: pointer;
    opacity: 0;
    pointer-events: none;
    transition: var(--maxuiux-transition-all-20s);

    @media (prefers-color-scheme: dark) {
        background-color: var(--maxuiux-color-black-rgba-045);
    }

    @include to(768px) {
        right: var(--maxuiux-spacing-44);
        bottom: var(--maxuiux-spacing-68);
    }

    @include to(687px) {
        right: var(--maxuiux-spacing-20);
        bottom: var(--maxuiux-spacing-20);
    }

    &.active {
        opacity: 1;
        pointer-events: all;
    }

    svg {
        width: 100%;
        height: 100%;
        color: var(--maxuiux-color-black);
        transition: color var(--maxuiux-transition-20s);

        @media (prefers-color-scheme: dark) {
            color: var(--maxuiux-color-white);
        }
    }

    &:hover {
        svg {
            color: var(--maxuiux-color-green-600);
        }
    }
}