.advantage {
    background-color: var(--maxuiux-color-green-100);

    @media (prefers-color-scheme: dark) {
        background-color: var(--maxuiux-color-green-1100);
    }

    &__blocks {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-auto-rows: var(--maxuiux-spacing-340);
        gap: var(--maxuiux-spacing-44);

        @include to(1366px) {
            grid-auto-rows: var(--maxuiux-spacing-240);
            gap: var(--maxuiux-spacing-40);
        }

        @include to(992px) {
            grid-auto-rows: var(--maxuiux-spacing-224);
            gap: var(--maxuiux-spacing-36);
        }

        @include to(768px) {
            grid-auto-rows: var(--maxuiux-spacing-208);
            gap: var(--maxuiux-spacing-26);
        }

        @include to(690px) {
            grid-auto-rows: var(--maxuiux-spacing-192);
        }

        @include to(576px) {
            grid-auto-rows: var(--maxuiux-spacing-160);
        }

        @include to(375px) {
            grid-auto-rows: var(--maxuiux-spacing-128);
        }
    }

    &__block {
        max-width: 100%;
        width: 100%;
        background-color: var(--maxuiux-color-green-200);
        background-repeat: no-repeat;
        background-size: auto;
        border-radius: var(--maxuiux-border-radius-large);
        position: relative;
        overflow: hidden;


        @media (prefers-color-scheme: dark) {
            background-color: var(--maxuiux-color-green-1000);
        }

        @include to(1366px) {
            background-size: 35%;
        }

        @include to(1280px) {
            background-size: 40%;
        }

        @include to(690px) {
            background-size: 300px;
        }

        @include to(992px) {

            &--2,
            &--3,
            &--4 {

                &::before,
                &::after {
                    content: "";
                    position: absolute;
                    z-index: var(--maxuiux-z-index-10);
                    width: var(--maxuiux-spacing-36);
                    height: var(--maxuiux-spacing-24);
                    background-color: var(--maxuiux-color-green-300);

                    @media (prefers-color-scheme: dark) {
                        background-color: var(--maxuiux-color-green-800);
                    }

                    @include to(576px) {
                        display: none;
                    }
                }
            }

            &--3 {

                &::before,
                &::after {
                    right: 0;
                    bottom: 0;
                }
            }

            &--2,
            &--4 {

                &::before,
                &::after {
                    left: 0;
                }

                &::before {
                    top: 0;
                }

                &::after {
                    bottom: 0;
                }
            }
        }

        &-num {
            font-size: var(--maxuiux-spacing-576);
            font-weight: var(--maxuiux-font-weight-bold);
            color: var(--maxuiux-color-green-300);
            user-select: none;
            position: absolute;
            z-index: var(--maxuiux-z-index-20);

            span:nth-child(2) {
                display: none;
            }

            @media (prefers-color-scheme: dark) {
                color: var(--maxuiux-color-green-800);
            }

            @include to(1366px) {
                font-size: var(--maxuiux-spacing-384);
            }

            @include to(992px) {
                font-size: var(--maxuiux-spacing-320);
            }

            @include to(690px) {
                font-size: var(--maxuiux-spacing-288);
            }

            @include to(576px) {
                font-size: var(--maxuiux-spacing-240);
            }

            @include to(530px) {
                span:nth-child(1) {
                    display: none;
                }

                span:nth-child(2) {
                    display: block;
                }
            }

            @include to(375px) {
                font-size: var(--maxuiux-spacing-208);
            }
        }

        &--1 {
            background-image: url(../img/advantage-block-img-bg-1.png);
            background-image: image-set(url(../img/advantage-block-img-bg-1.webp) type('image/webp'));
            background-position: left -140px top;

            @include to(530px) {
                background-position: left -145px top;
            }

            @include to(460px) {
                background-position: left -170px top -34px;
            }

            @include to(375px) {
                background-position: left -190px top -34px;
            }

            @include to(340px) {
                background-position: left -200px top -72px;
            }

            .advantage__block-num {
                top: -158px;
                right: -48px;

                @include to(1366px) {
                    top: -100px;
                    right: -34px;
                }

                @include to(992px) {
                    top: -76px;
                }

                @include to(690px) {
                    top: -70px;
                }

                @include to(576px) {
                    top: -60px;
                    right: -22px;
                }

                @include to(530px) {
                    top: -56px;
                    right: -20px;
                }
            }

            .advantage__block-wrap {
                justify-content: flex-end;
            }
        }

        &--2 {
            background-image: url(../img/advantage-block-img-bg-2.png);
            background-image: image-set(url(../img/advantage-block-img-bg-2.webp) type('image/webp'));
            background-position: right -90px top;

            @include to(530px) {
                background-position: right -140px top;
            }

            @include to(460px) {
                background-position: right -180px top -24px;
            }

            @include to(375px) {
                background-position: right -190px top -24px;
            }
            
            @include to(340px) {
                background-position: right -200px top -24px;
            }

            .advantage__block-num {
                top: -168px;
                left: -80px;

                @include to(1366px) {
                    top: -107px;
                    left: -64px;
                }

                @include to(992px) {
                    top: -76px;
                    left: -50px;
                }

                @include to(690px) {
                    top: -70px;
                }

                @include to(576px) {
                    top: -60px;
                    left: -40px;
                }

                @include to(530px) {
                    top: -62px;
                    left: -15px;
                }
            }
        }

        &--3 {
            background-image: url(../img/advantage-block-img-bg-3.png);
            background-image: image-set(url(../img/advantage-block-img-bg-3.webp) type('image/webp'));
            background-position: left -112px top;

            @include to(530px) {
                background-position: left -140px top;
            }

            @include to(460px) {
                background-position: left -170px top -62px;
            }

            @include to(375px) {
                background-position: left -190px top -62px;
            }

            .advantage__block-num {
                top: -168px;
                right: -90px;

                @include to(1366px) {
                    top: -107px;
                    right: -54px;
                }

                @include to(992px) {
                    top: -76px;
                }

                @include to(690px) {
                    top: -70px;
                    right: -48px;
                }

                @include to(576px) {
                    top: -60px;
                    right: -22px;
                }

                @include to(530px) {
                    top: -58px;
                    right: -37px;
                }
            }

            .advantage__block-wrap {
                justify-content: flex-end;
            }
        }

        &--4 {
            background-image: url(../img/advantage-block-img-bg-4.png);
            background-image: image-set(url(../img/advantage-block-img-bg-4.webp) type('image/webp'));
            background-position: right -100px top;

            @include to(530px) {
                background-position: right -145px top;
            }

            @include to(460px) {
                background-position: right -184px top -20px;
            }

            @include to(375px) {
                background-position: right -200px top -20px;
            }

            .advantage__block-num {
                top: -168px;
                left: -90px;

                @include to(1366px) {
                    top: -107px;
                    left: -64px;
                }

                @include to(992px) {
                    top: -76px;
                    left: -50px;
                }

                @include to(690px) {
                    top: -70px;
                }

                @include to(576px) {
                    top: -60px;
                    left: -40px;
                }

                @include to(530px) {
                    left: -46px;
                }
            }
        }

        &-wrap {
            display: flex;
            align-items: center;
            width: 100%;
            height: 100%;
            padding: var(--maxuiux-spacing-40);
            position: sticky;
            z-index: var(--maxuiux-z-index-30);

            @include to(690px) {
                padding: var(--maxuiux-spacing-26);
            }

            @include to(576px) {
                padding: var(--maxuiux-spacing-24);
            }
            
            @include to(448px) {
                padding: var(--maxuiux-spacing-20);
            }
        }

        &-title {
            font-size: var(--maxuiux-spacing-64);
            font-weight: var(--maxuiux-font-weight-extra-bold);
            color: var(--maxuiux-color-green-900);
            text-transform: uppercase;
            cursor: default;

            @media (prefers-color-scheme: dark) {
                color: var(--maxuiux-color-white-600);
            }

            @include to(1280px) {
                font-size: var(--maxuiux-spacing-60);
            }

            @include to(1200px) {
                font-size: var(--maxuiux-spacing-56);
            }

            @include to(1194px) {
                font-size: var(--maxuiux-spacing-52);
            }

            @include to(1024px) {
                font-size: var(--maxuiux-spacing-48);
            }

            @include to(992px) {
                font-size: var(--maxuiux-spacing-44);
            }

            @include to(834px) {
                font-size: var(--maxuiux-spacing-40);
            }

            @include to(780px) {
                font-size: var(--maxuiux-spacing-36);
            }

            @include to(690px) {
                font-size: var(--maxuiux-spacing-30);
            }

            @include to(650px) {
                font-size: var(--maxuiux-spacing-28);
            }

            @include to(576px) {
                font-size: var(--maxuiux-spacing-24);
            }

            @include to(448px) {
                font-size: var(--maxuiux-spacing-20);
            }
        }
    }
}