$size: 16;

@function rem($value) {
  $remValue: calc($value / $size) + rem; 
  @return $remValue;
}

@function remPx($pixels, $context: 16px) {
  @return calc($pixels/$context)+rem;
}

@mixin from($min_width) {
  @media screen and (min-width: $min_width) {
    @content;
  }
}

@mixin to($max_width) {
  @media screen and (max-width: $max_width) {
    @content;
  }
}

@mixin from-to($min_width, $max_width) {
  @media screen and (min-width: $min_width) and (max-width: $max_width) {
    @content;
  }
}

@mixin from-h($min_height) {
  @media screen and (min-height: $min_height) {
    @content;
  }
}

@mixin to-h($max_height) {
  @media screen and (max-height: $max_height) {
    @content;
  }
}

// ep - entry points
@mixin ep-to($max_width, $max_height) {
  @media screen and (max-width: $max_width) and (max-height: $max_height) {
    @content;
  }
}

@mixin no-btn ($display: "inline-block") {
  padding: 0;
  margin: 0;
  border: 0;
  background-color: transparent;
  border-radius: 0;
  cursor: pointer;
  appearance: none;
  display: #{$display};
}

@mixin transit ($tr: 0.25) {
  transition: all #{$tr}s;
}

@mixin container($max-width: "120rem", $padding: "2rem") {
  width: 100%;
  max-width: #{$max-width};
  padding: 0 #{$padding};
  margin: 0 auto;
}

@mixin font($pixels: "16", $weight: "400", $line_height: "1.2") {
  font-size: #{$pixels}px;
  font-weight: #{$weight};
  line-height: #{$line_height};
}