.portfolio {

    &__list {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        gap: var(--maxuiux-spacing-26) var(--maxuiux-spacing-64);

        @include to(1440px) {
            gap: var(--maxuiux-spacing-26) var(--maxuiux-spacing-56);
        }

        @include to(992px) {
            gap: var(--maxuiux-spacing-26) var(--maxuiux-spacing-48);
        }

        @include to(768px) {
            gap: var(--maxuiux-spacing-24) var(--maxuiux-spacing-36);
        }

        @include to(576px) {
            gap: var(--maxuiux-spacing-20) var(--maxuiux-spacing-26);
        }

        @include to(428px) {
            gap: var(--maxuiux-spacing-16) var(--maxuiux-spacing-24);
        }

        &-item {
            cursor: default;

            &.active {
                .portfolio__list-link-title {
                    color: var(--maxuiux-color-green-600);
                    border-color: transparent;
                }

                .portfolio__list-link-num {
                    color: var(--maxuiux-color-white);
                    background-color: var(--maxuiux-color-green-600);
                }

                .portfolio__list-link:hover {
                    .portfolio__list-link-title {
                        border-color: transparent;
                    }
                }
            }
        }

        &-link {
            display: flex;
            align-items: center;
            flex-direction: row;
            gap: var(--maxuiux-spacing-16);

            &-title {
                font-size: var(--maxuiux-spacing-72);
                line-height: 1.3;
                font-weight: var(--maxuiux-font-weight-medium);
                border-bottom: 2px solid var(--maxuiux-color-gray-200);
                transition: var(--maxuiux-transition-all-20s);

                @media (prefers-color-scheme: dark) {
                    border-color: var(--maxuiux-color-gray-900);
                }

                @include to(1440px) {
                    font-size: var(--maxuiux-spacing-64);
                }

                @include to(1200px) {
                    font-size: var(--maxuiux-spacing-56);
                }

                @include to(992px) {
                    font-size: var(--maxuiux-font-size-h1);
                }

                @include to(768px) {
                    font-size: var(--maxuiux-font-size-h2);
                }

                @include to(576px) {
                    font-size: var(--maxuiux-font-size-h3);
                    border-width: var(--maxuiux-border-width-small);
                }

                @include to(428px) {
                    font-size: var(--maxuiux-font-size-h4);
                }

                @include to(375px) {
                    font-size: var(--maxuiux-font-size-h5);
                }
            }

            &-num {
                display: flex;
                align-items: center;
                justify-content: center;
                padding: var(--maxuiux-spacing-12) var(--maxuiux-spacing-24);
                border-radius: var(--maxuiux-border-radius-full);
                font-size: var(--maxuiux-font-size-h3);
                font-weight: var(--maxuiux-font-weight-semi-bold);
                background-color: var(--maxuiux-color-gray-200);
                transition: var(--maxuiux-transition-all-20s);

                @media (prefers-color-scheme: dark) {
                    background-color: var(--maxuiux-color-gray-900);
                }

                @include to(1440px) {
                    padding: var(--maxuiux-spacing-10) var(--maxuiux-spacing-20);
                }

                @include to(1200px) {
                    font-size: var(--maxuiux-font-size-h4);
                    padding: var(--maxuiux-spacing-8) var(--maxuiux-spacing-20);
                }

                @include to(768px) {
                    font-size: var(--maxuiux-font-size-h5);
                    padding: var(--maxuiux-spacing-8) var(--maxuiux-spacing-14);
                }

                @include to(576px) {
                    font-size: var(--maxuiux-font-size-h6);
                }

                @include to(428px) {
                    padding: var(--maxuiux-spacing-6) var(--maxuiux-spacing-10);
                }
            }

            &:hover {
                .portfolio__list-link-title {
                    color: var(--maxuiux-color-green-600);
                    border-color: var(--maxuiux-color-green-600);
                }

                .portfolio__list-link-num {
                    color: var(--maxuiux-color-white);
                    background-color: var(--maxuiux-color-green-600);
                }
            }
        }
    }

    &__blocks {
        display: grid;
        grid-auto-flow: row dense;
        grid-template-columns: repeat(4, 1fr);
        gap: var(--maxuiux-spacing-32);

        @include to(1440px) {
            gap: var(--maxuiux-spacing-24);
        }

        @include to(992px) {
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__block {
        grid-column: auto/span 1;
        grid-row: auto/span 1;
        border: var(--maxuiux-border-width-small) solid var(--maxuiux-color-gray-200);
        border-radius: var(--maxuiux-border-radius-medium);
        overflow: hidden;

        @media (prefers-color-scheme: dark) {
            border-color: var(--maxuiux-color-gray-1100);
        }

        &-img {
            height: 100%;
            width: 100%;
            transform: scale(1);
            transition: var(--maxuiux-transition-all-20s);

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &:hover {
            .portfolio__block-img {
                transform: scale(1.1);
            }
        }
    }

    &.portfolio-main {
        .portfolio__block {
            &:nth-child(1) {
                grid-column: auto / span 3;
                grid-row: auto/span 2;
            }

            &:nth-child(2) {
                grid-row: auto/span 2;
            }

            &:nth-child(3),
            &:nth-child(4) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(7) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(10) {
                grid-row: auto/span 2;
            }

            &:nth-child(11) {
                grid-column: auto/span 3;
                grid-row: auto/span 2;
            }

            &:nth-child(12),
            &:nth-child(13),
            &:nth-child(14),
            &:nth-child(15) {
                grid-column: auto/span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(16) {
                grid-column: auto/span 3;
                grid-row: auto/span 2;
            }

            &:nth-child(17) {
                grid-row: auto/span 2;
            }

            &:nth-child(18),
            &:nth-child(19),
            &:nth-child(20),
            &:nth-child(21),
            &:nth-child(22) {
                grid-column: auto/span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(28) {
                grid-column: auto/span 3;
                grid-row: auto/span 2;
            }

            &:nth-child(30),
            &:nth-child(31),
            &:nth-child(32),
            &:nth-child(33) {
                grid-column: auto/span 2;
                grid-row: auto/span 2;
            }

            @include to(992px) {

                &:nth-child(1),
                &:nth-child(11),
                &:nth-child(16),
                &:nth-child(28) {
                    grid-column: auto/span 2;

                }
            }
        }
    }

    &.portfolio-site {
        .portfolio__block {
            &:nth-child(1) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(4) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(6) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(11) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(15) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(17),
            &:nth-child(18) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }
        }
    }

    &.portfolio-design {
        .portfolio__block {
            &:nth-child(1) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(4) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(6) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }
        }
    }

    &.portfolio-logo {
        .portfolio__blocks {
            grid-template-columns: repeat(3, 1fr);
        }

        .portfolio__block {
            &:nth-child(1) {
                grid-column: auto / span 3;
                grid-row: auto/span 2;
            }

            &:nth-child(2) {
                grid-column: auto / span 2;
                grid-row: auto/span 1;
            }
        }
    }

    &.portfolio-solutions {
        .portfolio__block {
            &:nth-child(1) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(4) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(6) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(11) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(15) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(17) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }
        }
    }

    &.portfolio-projects {
        .portfolio__block {

            &:nth-child(1),
            &:nth-child(2) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }
        }
    }
}