.container,
.container-xl,
.container-md,
.container-sm {
    width: 100%;
    margin: var(--maxuiux-spacing-0) auto;
    padding: var(--maxuiux-spacing-0) var(--maxuiux-spacing-20);
}

.container {
    max-width: 100%;
}

.container-xl {
    max-width: 1720px;
}

.container-md {
    max-width: 1440px;
}

.container-sm {
    max-width: 1200px;
}

.container-full {
    max-width: 1920px;
    width: 100%;
    margin: 0 auto;
}

.center {
    display: flex;
    justify-content: center;
}

.section-padding {
    padding-top: var(--maxuiux-spacing-144);
    padding-bottom: var(--maxuiux-spacing-144);

    @include to(1440px) {
        padding-top: var(--maxuiux-spacing-112);
        padding-bottom: var(--maxuiux-spacing-112);
    }

    @include to(992px) {
        padding-top: var(--maxuiux-spacing-80);
        padding-bottom: var(--maxuiux-spacing-80);
    }

    @include to(576px) {
        padding-top: var(--maxuiux-spacing-68);
        padding-bottom: var(--maxuiux-spacing-68);
    }
}

.section-padding-top {
    padding-top: var(--maxuiux-spacing-144);

    @include to(1440px) {
        padding-top: var(--maxuiux-spacing-112);
    }

    @include to(992px) {
        padding-top: var(--maxuiux-spacing-80);
    }

    @include to(576px) {
        padding-top: var(--maxuiux-spacing-68);
    }
}

.section-padding-bottom {
    padding-bottom: var(--maxuiux-spacing-144);

    @include to(1440px) {
        padding-bottom: var(--maxuiux-spacing-112);
    }

    @include to(992px) {
        padding-bottom: var(--maxuiux-spacing-80);
    }

    @include to(576px) {
        padding-bottom: var(--maxuiux-spacing-68);
    }
}

.btn {
    display: flex;
    align-items: center;
    flex-direction: row;
    user-select: none;
    font-weight: var(--maxuiux-font-weight-medium);
    padding: var(--maxuiux-spacing-12) var(--maxuiux-spacing-20);
    border-radius: var(--maxuiux-border-radius-medium);
    background-color: transparent;
    cursor: pointer;
    transition: background-color var(--maxuiux-transition-30s);

    svg {
        flex-shrink: 0;
        transition: var(--maxuiux-transition-all-20s);
    }
}

@media (hover: hover) {
    .btn:hover {
        background-color: var(--maxuiux-color-gray-300);
    }
}

@media (hover: none) {
    .btn:active {
        background-color: var(--maxuiux-color-gray-300);
    }
}

@media (prefers-color-scheme: dark) {

    .btn {
        color: var(--maxuiux-color-white);
    }

    @media (hover: hover) {
        .btn:hover {
            background-color: var(--maxuiux-color-gray-900);
        }
    }

    @media (hover: none) {
        .btn:hover {
            background-color: var(--maxuiux-color-gray-900);
        }
    }
}

.btn-pressing {
    backface-visibility: hidden;
    transform-style: preserve-3d;
    transform: scale(1);
    transition: var(--maxuiux-transition-all-20s);

    &:active {
        transform: scale(0.97);
    }
}

.scroll-margin-top-sm {
    scroll-margin-top: rem(45);
}

.scroll-margin-top-md {
    scroll-margin-top: rem(80);
}