.header {
    position: sticky;
    top: var(--maxuiux-spacing-0);
    left: var(--maxuiux-spacing-0);
    right: var(--maxuiux-spacing-0);
    z-index: var(--maxuiux-z-index-sticky);
    background-color: var(--maxuiux-color-white-rgba-060);
    backdrop-filter: var(--maxuiux-blur-large);

    @media (prefers-color-scheme: dark) {
        background-color: var(--maxuiux-color-black-rgba-045);
    }

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: var(--maxuiux-spacing-8) var(--maxuiux-spacing-0);
    }

    &__logo {
        max-width: rem(246);
        width: 100%;
        padding-bottom: var(--maxuiux-spacing-4);

        svg {
            width: rem(124);
            height: rem(28);
        }

        @include to(1440px) {
            max-width: initial;
            width: initial;
        }
    }

    &__menu {
        &-list {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;

            &-item {
                flex-shrink: 0;
                transform: scale(1);
            }

            &-link {
                gap: var(--maxuiux-spacing-4);
                position: relative;

                svg {
                    width: 16px;
                    height: 16px;
                    transition: var(--maxuiux-transition-all-20s);
                }
            }
        }
    }

    &-sub {
        position: absolute;
        left: 50%;
        top: 75%;
        z-index: var(--maxuiux-z-index-dropdown);
        transform: translateX(-50%);
        opacity: var(--maxuiux-spacing-0);
        pointer-events: none;
        transition: var(--maxuiux-transition-all-20s);

        &__menu {
            width: var(--maxuiux-spacing-288);
            padding: var(--maxuiux-spacing-4);
            background-color: var(--maxuiux-color-white);
            border-radius: var(--maxuiux-border-radius-large);
            border: var(--maxuiux-border-width-small);
            border-style: solid;
            border-color: var(--maxuiux-color-gray-200);
            box-shadow: var(--maxuiux-box-shadow-medium);
            transform: scale(0.9);
            transition: var(--maxuiux-transition-all-20s);

            &-list {
                display: flex;
                flex-direction: column;

                &-item {
                    &:nth-child(1) {
                        .header-sub__menu-list-link:hover {
                            svg {
                                color: var(--maxuiux-color-green-600);
                            }
                        }
                    }

                    &:nth-child(2) {
                        .header-sub__menu-list-link:hover {
                            svg {
                                color: var(--maxuiux-color-red-600);
                            }
                        }
                    }

                    &:nth-child(3) {
                        .header-sub__menu-list-link:hover {
                            svg {
                                color: var(--maxuiux-color-purple-600);
                            }
                        }
                    }

                    &:nth-child(4) {
                        .header-sub__menu-list-link:hover {
                            svg {
                                color: var(--maxuiux-color-pink-600);
                            }
                        }
                    }

                    &:nth-child(5) {
                        .header-sub__menu-list-link:hover {
                            svg {
                                color: var(--maxuiux-color-blue-600);
                            }
                        }
                    }

                    &:nth-child(6) {
                        .header-sub__menu-list-link:hover {
                            svg {
                                color: var(--maxuiux-color-orange-600);
                            }
                        }
                    }
                }

                &-link {
                    gap: var(--maxuiux-spacing-16);

                    svg {
                        flex-shrink: var(--maxuiux-spacing-0);
                        width: var(--maxuiux-spacing-26);
                        height: var(--maxuiux-spacing-26);
                    }

                    .icon-fill {
                        display: none;
                    }
                }
            }

            @media (prefers-color-scheme: dark) {
                background-color: var(--maxuiux-color-gray-1100);
                border-color: var(--maxuiux-color-gray-1000);
            }
        }
    }

    .header-sub__menu-list-item {
        &.active {
            .header-sub__menu-list-link {
                pointer-events: none;
                background-color: var(--maxuiux-color-gray-300);

                .icon-default {
                    display: none;
                }

                .icon-fill {
                    display: flex;
                }

                @media (prefers-color-scheme: dark) {
                    background-color: var(--maxuiux-color-gray-900);
                }
            }

            &:hover {
                svg {
                    color: var(--maxuiux-color-black);

                    @media (prefers-color-scheme: dark) {
                        color: var(--maxuiux-color-white);
                    }
                }
            }
        }
    }

    .header__menu-list-item-sub {
        &:hover {
            .header__menu-list-link {
                background-color: var(--maxuiux-color-gray-300);
            }

            .header-sub {
                opacity: var(--maxuiux-spacing-1);
                pointer-events: all;
                top: 100%;

                .header-sub__menu {
                    transform: scale(1);
                }
            }
        }

        @media (prefers-color-scheme: dark) {
            &:hover {
                .header__menu-list-link {
                    background-color: var(--maxuiux-color-gray-900);
                }
            }
        }
    }

    &__contact {
        &-blocks {
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: var(--maxuiux-spacing-2);
        }

        &-block {
            gap: var(--maxuiux-spacing-8);

            &-text {
                @include to(1580px) {
                    display: none;
                }
            }

            &-icon {
                svg {
                    width: var(--maxuiux-spacing-24);
                    height: var(--maxuiux-spacing-24);
                }
            }

            &:hover {
                background-color: var(--maxuiux-color-gray-300);
            }


        }
    }

    @include to(1366px) {

        &__menu,
        &__contact {
            display: none;
        }
    }

    &__nav {
        display: none;

        &-btn {
            svg {
                width: var(--maxuiux-spacing-24);
                height: var(--maxuiux-spacing-24);
            }
        }

        @include to(1366px) {
            display: block;
        }
    }
}