.header-info {
    padding-top: var(--maxuiux-spacing-68);

    @include to(1370px) {
        padding-top: var(--maxuiux-spacing-64);
    }

    @include to(1194px) {
        padding-top: var(--maxuiux-spacing-44);
    }

    @include to(1020px) {
        padding-top: var(--maxuiux-spacing-40);
    }

    @include to(992px) {
        padding-top: var(--maxuiux-spacing-36);
    }

    @include to(768px) {
        padding-top: var(--maxuiux-spacing-32);
    }

    @include to(576px) {
        padding-top: var(--maxuiux-spacing-26);
    }

    &__blocks {
        display: grid;
        grid-template-columns: 645px 1fr 1fr;
        grid-auto-rows: var(--maxuiux-spacing-256);
        gap: var(--maxuiux-spacing-40);

        @media (prefers-color-scheme: dark) {
            position: relative;

            &:before {
                content: "";
                position: absolute;
                z-index: var(--maxuiux-z-index-m-1);
                top: var(--maxuiux-spacing-0);
                right: var(--maxuiux-spacing-0);
                bottom: var(--maxuiux-spacing-0);
                left: var(--maxuiux-spacing-0);
                background: linear-gradient(140deg, var(--maxuiux-color-purple-600) 0%, var(--maxuiux-color-green-600) 90%);
                transform: translate3d(-10px, 0, 0) scale(1.01);
                filter: blur(50px);
                opacity: 0.5;
                transition: opacity 0.3s;
                border-radius: inherit;
            }
        }

        @include to(1564px) {
            grid-template-columns: 600px 1fr 1fr;
        }

        @include to(1370px) {
            grid-template-columns: 480px 1fr 1fr;
            grid-auto-rows: var(--maxuiux-spacing-224);
            gap: var(--maxuiux-spacing-20);
        }

        @include to(1220px) {
            grid-auto-rows: var(--maxuiux-spacing-208);
        }

        @include to(1020px) {
            grid-template-columns: 1fr 1fr;
            grid-auto-rows: var(--maxuiux-spacing-208);
        }

        @include to(992px) {
            grid-auto-rows: var(--maxuiux-spacing-192);
        }

        @include to(768px) {
            grid-auto-rows: var(--maxuiux-spacing-176);
        }

        @include to(576px) {
            grid-auto-rows: var(--maxuiux-spacing-160);
        }

        @include to(430px) {
            grid-auto-rows: var(--maxuiux-spacing-128);
        }

        @include to(320px) {
            grid-auto-rows: var(--maxuiux-spacing-96);
        }
    }

    &__block {
        overflow: hidden;
        background-color: var(--maxuiux-color-gray-200);
        background-repeat: no-repeat;
        background-size: auto;
        border-radius: var(--maxuiux-spacing-20);
        transition: var(--maxuiux-transition-all-20s);

        @media (prefers-color-scheme: dark) {
            background-color: var(--maxuiux-color-black-rgba-080);
            backdrop-filter: var(--maxuiux-blur-small);

            &:hover {
                background-color: var(--maxuiux-color-black-rgba-060);
            }

            @media(hover: hover) {
                &:hover {
                    background-color: var(--maxuiux-color-black-rgba-060);
                }
            }

            @media(hover: none) {
                &:hover {
                    background-color: var(--maxuiux-color-black-rgba-060);
                }
            }
        }

        @include to(448px) {
            background-size: 40%;
            background-position: center right -50px;
        }

        &-wrap {
            width: 100%;
            height: 100%;
            padding: var(--maxuiux-spacing-44);

            @include to(1370px) {
                padding: var(--maxuiux-spacing-40);
            }

            @include to(1194px) {
                padding: var(--maxuiux-spacing-36);
            }

            @include to(576px) {
                padding: var(--maxuiux-spacing-24);
            }

            @include to(448px) {
                padding: var(--maxuiux-spacing-20);
            }
        }

        &-img {
            user-select: none;
            margin-bottom: var(--maxuiux-spacing-36);

            @include to(1370px) {
                margin-bottom: var(--maxuiux-spacing-30);
            }

            img {
                max-width: 100%;
                height: auto;
                border-bottom-left-radius: var(--maxuiux-spacing-20);
                border-bottom-right-radius: var(--maxuiux-spacing-20);
            }
        }

        &-title {
            font-display: swap;
            font-size: var(--maxuiux-font-size-h1);
            font-weight: var(--maxuiux-font-weight-bold);

            @include to(1370px) {
                font-size: var(--maxuiux-font-size-h2);
            }

            @include to(1194px) {
                font-size: var(--maxuiux-font-size-h3);
            }

            @include to(576px) {
                font-size: var(--maxuiux-font-size-h4);
            }

            @include to(448px) {
                font-size: var(--maxuiux-font-size-h5);
            }
        }

        &-icon {
            svg {
                width: var(--maxuiux-spacing-44);
                height: var(--maxuiux-spacing-44);

                @include to(1370px) {
                    width: var(--maxuiux-spacing-36);
                    height: var(--maxuiux-spacing-36);
                }

                @include to(576px) {
                    width: var(--maxuiux-spacing-28);
                    height: var(--maxuiux-spacing-28);
                }

                @include to(448px) {
                    width: var(--maxuiux-spacing-26);
                    height: var(--maxuiux-spacing-26);
                }
            }
        }

        &--1 {
            grid-row: 1 / 4;
            background-image: url(../img/header-info-block-img-bg-1.png);
            background-image: image-set(url(../img/header-info-block-img-bg-1.webp) type('image/webp'));
            background-position: bottom -252px right -380px;

            @include to(1580px) {
                background-position: bottom -240px right -424px;
            }

            @include to(1370px) {
                background-position: bottom -190px right -560px;
            }

            @include to(1220px) {
                grid-row: 1 / 5;
            }

            @include to(1020px) {
                grid-column: auto / span 2;
                grid-row: 1 / 3;
                background-position: bottom -340px right -490px;

                .header-info__block-wrap {
                    display: flex;
                    flex-direction: row;
                    gap: var(--maxuiux-spacing-36);
                    padding: var(--maxuiux-spacing-36) var(--maxuiux-spacing-20) var(--maxuiux-spacing-0) var(--maxuiux-spacing-0);
                }

                .header-info__block-img {
                    margin-bottom: initial;
                }
            }

            @include to(720px) {
                .header-info__block-wrap {
                    gap: initial;
                }

                .header-info__block-img {
                    img {
                        border-radius: initial;
                    }
                }
            }

            @include to(540px) {
                background-position: bottom -320px right -580px;
            }

            @include to(448px) {
                background-position: bottom right -50px;
            }

            @include to(270px) {
                .header-info__block-img {
                    display: flex;
                    align-self: flex-end;
                }
            }
        }

        &--2 {
            grid-column: auto / span 2;
            background-image: url(../img/header-info-block-img-bg-2.png);
            background-image: image-set(url(../img/header-info-block-img-bg-2.webp) type('image/webp'));
            background-position: bottom -90px right -86px;

            @include to(1580px) {
                background-position: bottom -90px right -120px;
            }

            @include to(1490px) {
                background-position: bottom -120px right -172px;
            }

            @include to(540px) {
                background-position: bottom -70px right -272px;
            }
        }

        &--3 {
            grid-column: auto / span 2;
            background-image: url(../img/header-info-block-img-bg-3.png);
            background-image: image-set(url(../img/header-info-block-img-bg-3.webp) type('image/webp'));
            background-position: bottom -190px right -152px;

            @include to(1580px) {
                background-position: bottom -190px right -197px;
            }

            @include to(1490px) {
                background-position: bottom -160px right -234px;
            }

            @include to(540px) {
                background-position: bottom -190px right -340px;
            }
        }

        &--4 {
            grid-column: auto / span 1;
            background-image: url(../img/header-info-block-img-bg-4.png);
            background-image: image-set(url(../img/header-info-block-img-bg-4.webp) type('image/webp'));
            background-position: bottom -80px right -60px;

            @include to(1580px) {
                background-position: bottom -80px right -100px;
            }

            @include to(1490px) {
                background-position: bottom -80px right -140px;
            }

            @include to(1220px) {
                grid-column: auto / span 2;
            }

            @include to(1020px) {
                grid-column: auto / span 1;
            }

            @include to(768px) {
                grid-column: auto / span 2;
            }

            @include to(540px) {
                background-position: bottom -140px right -240px;
            }
        }

        &--5 {
            grid-column: auto / span 1;
            background-image: url(../img/header-info-block-img-bg-5.png);
            background-image: image-set(url(../img/header-info-block-img-bg-5.webp) type('image/webp'));
            background-position: bottom -91px right -94px;

            @include to(1580px) {
                background-position: bottom -91px right -134px;
            }

            @include to(1490px) {
                background-position: bottom -91px right -174px;
            }

            @include to(1220px) {
                grid-column: auto / span 2;
            }

            @include to(1020px) {
                grid-column: auto / span 1;
            }

            @include to(768px) {
                grid-column: auto / span 2;
            }

            @include to(540px) {
                background-position: bottom -180px right -274px;
            }
        }

        &:hover {
            background-color: var(--maxuiux-color-gray-300);
        }

        @media(hover: hover) {
            &:hover {
                background-color: var(--maxuiux-color-gray-300);
            }
        }

        @media(hover: none) {
            &:hover {
                background-color: var(--maxuiux-color-gray-300);
            }
        }
    }
}