.design {

    &__blocks {
        display: grid;
        grid-auto-flow: row dense;
        grid-template-columns: repeat(4, 1fr);
        gap: var(--maxuiux-spacing-26);

        @media (prefers-color-scheme: dark) {
            position: relative;

            &:before {
                content: "";
                position: absolute;
                z-index: var(--maxuiux-z-index-m-1);
                top: var(--maxuiux-spacing-0);
                right: var(--maxuiux-spacing-0);
                bottom: var(--maxuiux-spacing-0);
                left: var(--maxuiux-spacing-0);
                background: linear-gradient(140deg, var(--maxuiux-color-blue-600) 0%, var(--maxuiux-color-green-600) 50%, var(--maxuiux-color-blue-600) 90%);
                transform: translate3d(-10px, 0, 0) scale(1.01);
                filter: blur(70px);
                opacity: 0.5;
                transition: opacity 0.3s;
                border-radius: inherit;
            }
        }
        
        @include to(576px) {
            gap: var(--maxuiux-spacing-20);
        }
    }

    &__block {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: var(--maxuiux-spacing-52);
        gap: var(--maxuiux-spacing-26);
        grid-row: auto/span 1;
        grid-column: auto / span 2;
        background-color: var(--maxuiux-color-gray-200);
        border-radius: var(--maxuiux-border-radius-medium);
        transition: var(--maxuiux-transition-all-20s);
        overflow: hidden;

        @media (prefers-color-scheme: dark) {
            background-color: var(--maxuiux-color-black-rgba-080);
            backdrop-filter: var(--maxuiux-blur-small);

            &:hover {
                background-color: var(--maxuiux-color-black-rgba-070);
            }
        }

        @include to(992px) {
            padding: var(--maxuiux-spacing-52) var(--maxuiux-spacing-40);
        }

        &--1 {
            &:hover {
                .design__block-subtitle {
                    color: var(--maxuiux-color-green-600);
                }
            }
        }

        &--2 {
            &:hover {
                .design__block-subtitle {
                    color: var(--maxuiux-color-blue-600);
                }
            }
        }

        &--3 {
            &:hover {
                .design__block-subtitle {
                    color: var(--maxuiux-color-orange-600);
                }
            }
        }

        &--4 {
            &:hover {
                .design__block-subtitle {
                    color: var(--maxuiux-color-pink-600);
                }
            }
        }

        &--5 {
            grid-row: auto/span 2;

            &:hover {
                .design__block-title {
                    color: var(--maxuiux-color-purple-600);
                }
            }
        }

        &-title {
            font-size: var(--maxuiux-font-size-h1);

            @include to(992px) {
                font-size: var(--maxuiux-font-size-h2);
            }

            @include to(768px) {
                font-size: var(--maxuiux-font-size-h3);
            }

            @include to(576px) {
                font-size: var(--maxuiux-font-size-h4);
            }
        }

        &-subtitle {
            font-size: var(--maxuiux-font-size-h3);

            @include to(992px) {
                font-size: var(--maxuiux-font-size-h4);
            }

            @include to(768px) {
                font-size: var(--maxuiux-font-size-h5);
            }

            @include to(576px) {
                font-size: var(--maxuiux-font-size-h6);
            }
        }

        &-title,
        &-subtitle {
            cursor: default;
            transition: color var(--maxuiux-transition-20s);
        }

        &-img {
            max-width: 256px;
            width: 100%;
            max-height: 256px;
            height: 100%;
            user-select: none;
            transform: scale(1);
            transition: var(--maxuiux-transition-all-20s);
        }

        &:active {
            .design__block-img {
                transform: scale(0.9);
            }
        }
    }
}