.sitemap {

    &__blocks {
        display: flex;
        flex-direction: column;
        gap: var(--maxuiux-spacing-36);
    }

    &__block {
        display: flex;
        flex-direction: column;
        gap: var(--maxuiux-spacing-24);
        
        @include to(768px) { 
            gap: var(--maxuiux-spacing-16);
        }
    }

    &__title {
        @include to(768px) {
            font-size: var(--maxuiux-font-size-h4);
        }
    }

    &__list {

        &-link {
            gap: var(--maxuiux-spacing-4);

            svg {
                width: 16px;
                height: 16px;
                transition: var(--maxuiux-transition-all-20s);
            }
        }
    }
}