.footer {
    border-top: var(--maxuiux-spacing-1) solid var(--maxuiux-color-gray-200);

    @media (prefers-color-scheme: dark) {
        border-color: var(--maxuiux-color-gray-900);
    }

    &__wrap {
        padding: 0 var(--maxuiux-spacing-44);
    }

    &-menu {
        padding: var(--maxuiux-spacing-36) var(--maxuiux-spacing-0);

        &__items {
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            gap: var(--maxuiux-spacing-32) var(--maxuiux-spacing-128);
        }

        &__title {
            font-weight: var(--maxuiux-font-weight-bold);
            margin-bottom: var(--maxuiux-spacing-12);
        }

        &__list {
            display: flex;
            flex-direction: column;
            gap: var(--maxuiux-spacing-8);

            &-item {
                font-size: var(--maxuiux-font-size-small);
            }

            &-link {
                transition: color var(--maxuiux-transition-20s);

                &:hover {
                    color: var(--maxuiux-color-green-600);
                }
            }
        }
    }

    &-info {
        border-top: var(--maxuiux-spacing-1) solid var(--maxuiux-color-gray-200);
        padding: var(--maxuiux-spacing-36) var(--maxuiux-spacing-0);

        @media (prefers-color-scheme: dark) {
            border-color: var(--maxuiux-color-gray-900);
        }

        &__items {
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            gap: var(--maxuiux-spacing-32) var(--maxuiux-spacing-24);
        }

        &__logo {
            svg {
                width: var(--maxuiux-spacing-40);
                height: var(--maxuiux-spacing-40);
                transition: color var(--maxuiux-transition-20s);

            }
        }

        // &__version {
        //     transition: color var(--maxuiux-transition-20s);

        //     &:hover {
        //         color: var(--maxuiux-color-green-600);
        //     }
        // }
    }
}