.portfolio-open {
    padding-bottom: var(--maxuiux-spacing-112);

    @include to(992px) {
        padding-bottom: var(--maxuiux-spacing-80);
    }

    @include to(576px) {
        padding-bottom: var(--maxuiux-spacing-68);
    }

    &__header {
        display: flex;
        flex-direction: column;
        gap: var(--maxuiux-spacing-52);
        margin-bottom: var(--maxuiux-spacing-96);

        @include to(1440px) {
            gap: var(--maxuiux-spacing-48);
            margin-bottom: var(--maxuiux-spacing-80);
        }

        @include to(1200px) {
            margin-bottom: var(--maxuiux-spacing-72);
        }

        @include to(992px) {
            gap: var(--maxuiux-spacing-40);
        }

        @include to(576px) {
            gap: var(--maxuiux-spacing-32);
            margin-bottom: var(--maxuiux-spacing-60);
        }
    }

    &__title {
        font-size: var(--maxuiux-spacing-72);
        font-weight: var(--maxuiux-font-weight-medium);

        @media (prefers-color-scheme: dark) {
            border-color: var(--maxuiux-color-gray-900);
        }

        @include to(1440px) {
            font-size: var(--maxuiux-spacing-64);
        }

        @include to(1200px) {
            font-size: var(--maxuiux-spacing-56);
        }

        @include to(992px) {
            font-size: var(--maxuiux-font-size-h1);
        }

        @include to(768px) {
            font-size: var(--maxuiux-font-size-h2);
        }

        @include to(576px) {
            font-size: var(--maxuiux-font-size-h3);
        }

        @include to(428px) {
            font-size: var(--maxuiux-font-size-h4);
        }

        @include to(375px) {
            font-size: var(--maxuiux-font-size-h5);
        }
    }

    &__tags {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        gap: var(--maxuiux-spacing-24);
    }

    &__tag {
        color: var(--maxuiux-color-gray-700);
        font-weight: var(--maxuiux-font-weight-medium);

        @media (prefers-color-scheme: dark) {
            color: var(--maxuiux-color-gray-400);
        }
    }

    &__description {
        display: flex;
        flex-direction: column;
        gap: var(--maxuiux-spacing-96);

        @include to(1440px) {
            gap: var(--maxuiux-spacing-80);
        }

        @include to(1200px) {
            gap: var(--maxuiux-spacing-72);
        }

        @include to(576px) {
            gap: var(--maxuiux-spacing-60);
        }
    }

    &__img {
        img {
            max-width: 100%;
            height: auto;
        }
    }

    &__text {
        display: flex;
        flex-direction: column;
        gap: var(--maxuiux-spacing-24);

        .sections {
            display: flex;
            flex-direction: column;
            gap: var(--maxuiux-spacing-52);
            padding-top: var(--maxuiux-spacing-44);

            @include to(1440px) {
                padding-top: var(--maxuiux-spacing-40);
            }

            @include to(1200px) {
                gap: var(--maxuiux-spacing-44);
                padding-top: var(--maxuiux-spacing-36);
            }

            @include to(768px) {
                gap: var(--maxuiux-spacing-36);
            }

            @include to(576px) {
                padding-top: var(--maxuiux-spacing-32);
            }
        }

        .section {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: var(--maxuiux-spacing-24);

            @include to(1200px) {
                gap: var(--maxuiux-spacing-20);
            }
        }

        h2 {
            text-align: center;
            font-weight: var(--maxuiux-font-weight-medium);

            @include to(1200px) {
                font-size: var(--maxuiux-font-size-h3);
            }

            @include to(768px) {
                font-size: var(--maxuiux-font-size-h4);
            }

            @include to(576px) {
                font-size: var(--maxuiux-font-size-h5);
            }
        }

        p {
            max-width: var(--maxuiux-spacing-576);
            width: 100%;
            margin: var(--maxuiux-spacing-0) auto;
            padding: var(--maxuiux-spacing-0) var(--maxuiux-spacing-20);
            text-align: center;
            font-size: var(--maxuiux-font-size-h6);
            line-height: var(--maxuiux-line-height-medium);
        }

        img {
            max-width: 100%;
            height: auto;
            padding: var(--maxuiux-spacing-72) var(--maxuiux-spacing-0);

            @include to(1440px) {
                padding: var(--maxuiux-spacing-56) var(--maxuiux-spacing-0);
            }

            @include to(1200px) {
                padding: var(--maxuiux-spacing-48) var(--maxuiux-spacing-0);
            }

            @include to(576px) {
                padding: var(--maxuiux-spacing-36) var(--maxuiux-spacing-0);
            }
        }

        a {
            align-self: flex-start;
            display: inline-block;
            text-align: center;
            margin: 0 auto;
            font-size: var(--maxuiux-spacing-26);
            font-weight: var(--maxuiux-font-weight-medium);
            border-bottom: var(--maxuiux-border-width-medium) solid var(--maxuiux-color-gray-300);
            transition: var(--maxuiux-transition-all-20s);

            &:hover {
                color: var(--maxuiux-color-green-600);
                border-color: var(--maxuiux-color-green-600);
            }

            @include to(1200px) {
                font-size: var(--maxuiux-spacing-24);
            }

            @include to(768px) {
                font-size: var(--maxuiux-spacing-20);
            }

            @include to(576px) {
                font-size: var(--maxuiux-spacing-16);
                border-width: var(--maxuiux-border-width-small);
            }
        }
    }

    &-gallery {
        &__blocks {
            display: grid;
            background-color: var(--maxuiux-color-gray-100);

            @media (prefers-color-scheme: dark) {
                background-color: var(--maxuiux-color-gray-1100);
            }

            &.grid-1 {
                grid-template-columns: repeat(1, 1fr);

                .portfolio-open-gallery__block-img {
                    &:hover {
                        transform: initial;
                    }
                }
            }

            &.grid-2 {
                grid-template-columns: repeat(2, 1fr);
            }

            &.grid-3 {
                grid-template-columns: repeat(3, 1fr);

                @include to(992px) {
                    grid-template-columns: repeat(2, 1fr);
                }
            }
            
            &.grid-4 {
                grid-template-columns: repeat(4, 1fr);

                @include to(1200px) {
                    grid-template-columns: repeat(3, 1fr);
                }

                @include to(992px) {
                    grid-template-columns: repeat(2, 1fr);
                }
            }
        }

        &__block {
            cursor: zoom-in;
            overflow: hidden;

            &-img {
                height: 100%;
                width: 100%;
                transform: scale(1);
                transition: var(--maxuiux-transition-all-20s);

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            &:hover {
                .portfolio-open-gallery__block-img {
                    transform: scale(1.03);
                }
            }
        }
    }

    &__blocks {
        display: flex;
    }

    &__block {
        display: flex;
        flex-direction: column;
        gap: var(--maxuiux-spacing-24);

        @include to(1200px) {
            gap: var(--maxuiux-spacing-20);
        }

        &-title {
            font-weight: var(--maxuiux-font-weight-medium);
            color: var(--maxuiux-color-gray-700);
            cursor: default;

            @media (prefers-color-scheme: dark) {
                color: var(--maxuiux-color-gray-400);
            }
        }

        &-subtitle {
            line-height: var(--maxuiux-line-height-medium);
        }

        &-btn {
            gap: var(--maxuiux-spacing-8);
            color: var(--maxuiux-color-blue-700);

            @media (prefers-color-scheme: dark) {
                color: var(--maxuiux-color-white);
            }

            svg {
                width: var(--maxuiux-spacing-24);
                height: var(--maxuiux-spacing-24);
            }

            &:hover {
                background-color: var(--maxuiux-color-blue-100);
            }

            @media (hover: hover) {
                &:hover {
                    background-color: var(--maxuiux-color-blue-100);
                }
            }

            @media (hover: none) {
                &:hover {
                    background-color: var(--maxuiux-color-blue-100);
                }
            }
        }
    }
}