html {
  box-sizing: border-box;
  scroll-behavior: smooth;

  // @include to(1194px) {
  //   scrollbar-gutter: stable;
  // }
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}


body {
  font-family: var(--maxuiux-font-base);
  font-size: var(--maxuiux-font-size-medium);
  background: var(--maxuiux-color-white);
  color: var(--maxuiux-color-black);
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100dvh;
  position: relative;

  @media (prefers-color-scheme: dark) {
    background: var(--maxuiux-color-black);
    color: var(--maxuiux-color-white);
  }
}

.overflow {
  overflow: hidden;

}

img,
video {
  display: flex;
  max-width: 100%;
}

a {
  text-decoration: none;
  color: var(--maxuiux-color-black);

  @media (prefers-color-scheme: dark) {
    color: var(--maxuiux-color-white);
  }
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
li {
  list-style: none;
  padding: 0;
}

button,
input,
label {
  border: initial;
  outline: none;
}

svg {
  display: flex;

  @media (prefers-color-scheme: dark) {
    color: var(--maxuiux-color-white);
  }
}

h1 {
  font-size: var(--maxuiux-font-size-h1);
}

h2 {
  font-size: var(--maxuiux-font-size-h2);
}

h3 {
  font-size: var(--maxuiux-font-size-h3);
}

h4 {
  font-size: var(--maxuiux-font-size-h4);
}

h5 {
  font-size: var(--maxuiux-font-size-h5);
}

h6 {
  font-size: var(--maxuiux-font-size-h6);
}

// selection-green
::selection,
::selection {
  background-color: rgba(#28C065, 0.68);
  color: var(--maxuiux-color-white);
}

::-moz-selection,
::-moz-selection {
  background-color: rgba(#28C065, 0.68);
  color: var(--maxuiux-color-white);
}

::-webkit-selection,
::-webkit-selection {
  background-color: rgba(#28C065, 0.68);
  color: var(--maxuiux-color-white);
}
