.faq {
    padding-bottom: var(--maxuiux-spacing-26);

    &__block {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: var(--maxuiux-spacing-56);
        background-color: var(--maxuiux-color-gray-200);
        border-radius: var(--maxuiux-border-radius-medium);
        overflow: hidden;

        @media (prefers-color-scheme: dark) {
            background-color: var(--maxuiux-color-gray-1000);
        }

        &-img {
            user-select: none;
            transform: scale(1);
            transition: var(--maxuiux-transition-all-20s);
        }

        &:active {
            .faq__block-img {
                transform: scale(0.9);
            }
        }
    }
}