.support {

    &__blocks {
        display: flex;
        flex-direction: column;
        gap: var(--maxuiux-spacing-128);
        padding-top: var(--maxuiux-spacing-128);

        @include to(1194px) {
            padding-top: var(--maxuiux-spacing-112);
            gap: var(--maxuiux-spacing-112);
        }

        @include to(992px) {
            padding-top: var(--maxuiux-spacing-80);
            gap: var(--maxuiux-spacing-80);
        }

        @include to(576px) {
            padding-top: var(--maxuiux-spacing-68);
            gap: var(--maxuiux-spacing-68);
        }
    }

    &-header {
        &__blocks {
            display: flex;
            justify-content: space-between;

            @include to(1020px) {
                flex-direction: column-reverse;
                gap: var(--maxuiux-spacing-32);
            }

        }

        &__block {
            &-items {
                display: flex;
                flex-direction: column;
                align-items: flex-end;

                @include to(1020px) {
                    flex-direction: row;
                    flex-wrap: wrap;
                    align-items: center;
                    justify-content: flex-end;
                }
            }

            &-item {
                gap: var(--maxuiux-spacing-8);

                span {
                    text-wrap: nowrap;
                }

                svg {
                    width: var(--maxuiux-spacing-24);
                    height: var(--maxuiux-spacing-24);
                }
            }
        }

        &__message {
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            gap: var(--maxuiux-spacing-32);

            @include to(1194px) {
                flex-direction: column-reverse;
            }

            &-img {
                flex-shrink: 0;
                margin-top: var(--maxuiux-spacing-30);
                width: var(--maxuiux-spacing-128);
                height: var(--maxuiux-spacing-128);
                border-radius: var(--maxuiux-border-radius-full);
                border: var(--maxuiux-border-width-small) solid var(--maxuiux-color-gray-300);
                overflow: hidden;

                @include to(1194px) {
                    margin-top: initial;
                }

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    object-position: center;
                }
            }

            &-text {
                max-width: rem(616);
                font-size: var(--maxuiux-spacing-24);
                font-weight: var(--maxuiux-font-weight-medium);
                padding: var(--maxuiux-spacing-20) var(--maxuiux-spacing-26);
                border: var(--maxuiux-border-width-small) solid var(--maxuiux-color-gray-300);
                border-top-left-radius: var(--maxuiux-border-radius-xxl);
                border-top-right-radius: var(--maxuiux-border-radius-xxl);
                border-bottom-right-radius: var(--maxuiux-border-radius-xxl);

                @include to(1020px) {
                    font-size: var(--maxuiux-spacing-20);
                }
            }
        }
    }

    &__title {
        text-align: center;
        font-size: var(--maxuiux-spacing-40);
        margin-bottom: var(--maxuiux-spacing-96);

        @include to(1194px) {
            margin-bottom: var(--maxuiux-spacing-72);
        }

        @include to(992px) {
            font-size: var(--maxuiux-spacing-32);
            margin-bottom: var(--maxuiux-spacing-60);
        }

        @include to(576px) {
            font-size: var(--maxuiux-spacing-24);
            margin-bottom: var(--maxuiux-spacing-48);
        }
    }

    &__items {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: var(--maxuiux-spacing-32);

        @include to(1194px) {
            grid-template-columns: repeat(1, 1fr);
            gap: var(--maxuiux-spacing-24);
        }
    }

    &__item {
        display: flex;
        flex-direction: row;
        gap: var(--maxuiux-spacing-24);
        background-color: var(--maxuiux-color-white);
        border-radius: var(--maxuiux-border-radius-xxl);
        box-shadow: var(--maxuiux-box-shadow-green);
        padding: var(--maxuiux-spacing-16);
        overflow: hidden;

        @include to(992px) {
            padding: var(--maxuiux-spacing-12);
        }

        @include to(476px) {
            flex-direction: column;
            gap: var(--maxuiux-spacing-16);
        }

        @media (prefers-color-scheme: dark) {
            background-color: var(--maxuiux-color-gray-1000);
        }

        &-img {
            flex-shrink: 0;
            width: var(--maxuiux-spacing-256);
            height: var(--maxuiux-spacing-144);
            border-radius: var(--maxuiux-border-radius-xl);
            overflow: hidden;

            @include to(1194px) {
                width: var(--maxuiux-spacing-208);
                height: var(--maxuiux-spacing-112);
            }

            @include to(476px) {
                max-width: 100%;
                width: 100%;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;
                scale: 1;
                transition: var(--maxuiux-transition-all-20s);
            }
        }

        &-wrap {
            display: flex;
            flex-direction: column;
            gap: var(--maxuiux-spacing-14);
            letter-spacing: -0.02em;
        }

        &-title {
            font-size: var(--maxuiux-font-size-medium);
            font-weight: var(--maxuiux-font-weight-semi-bold);
        }

        &-text {
            font-size: var(--maxuiux-font-size-small);
            line-height: 140%;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        @media (hover: hover) {
            &:hover {
                .support__item-img {
                    img {
                        scale: 1.06;
                    }
                }
            }
        }

        @media (hover: none) {
            &:active {
                .support__item-img {
                    img {
                        scale: 1.06;
                    }
                }
            }
        }
    }

    &-social {
        &__list {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;

            &-link {

                svg {
                    width: var(--maxuiux-spacing-36);
                    height: var(--maxuiux-spacing-36);
                    transition: all 0.25s cubic-bezier(.4, 0, .2, 1);
                }

                @media (hover: hover) {
                    &:hover {
                    background-color: var(--maxuiux-color-black-rgba-005);
                    
                        svg {
                            color: var(--maxuiux-color-green-600);
                        }
                    }
                }

                @media (hover: none) {
                    &:active {
                    background-color: var(--maxuiux-color-black-rgba-020);
                    
                        svg {
                            color: var(--maxuiux-color-green-600);
                        }
                    }
                }
            }
        }
    }

    &-me {
        &__wrap {
            display: flex;
            flex-direction: row;
            justify-content: center;
            gap: var(--maxuiux-spacing-40);

            @include to(768px) {
                flex-direction: column;
                gap: var(--maxuiux-spacing-24);
            }
        }

        &__info {
            flex-basis: rem(444);
            display: flex;
            flex-direction: column;
            gap: var(--maxuiux-spacing-20);
            padding: var(--maxuiux-spacing-24);
            border-radius: var(--maxuiux-border-radius-xxl);
            border: var(--maxuiux-border-width-small) solid var(--maxuiux-color-gray-300);
            overflow: hidden;

            @include to(768px) {
                flex-basis: auto;
            }

            &-title {
                font-size: var(--maxuiux-line-height-small);
                font-weight: var(--maxuiux-font-weight-semi-bold);
            }

            &-text {
                display: flex;
                flex-direction: column;
                gap: var(--maxuiux-spacing-16);
                font-size: var(--maxuiux-line-height-tiny);
                line-height: 160%;
                letter-spacing: -0.02em;
            }
        }
    }

    &-qr {
        &__items {
            display: flex;
            flex-direction: column;
            gap: var(--maxuiux-spacing-32);

            @include to(768px) {
                flex-direction: row;
                gap: var(--maxuiux-spacing-24);
            }

            @include to(476px) {
                flex-direction: column;
            }
        }

        &__item {
            display: flex;
            flex-direction: column;
            gap: var(--maxuiux-spacing-16);
            padding: var(--maxuiux-spacing-16);
            border-radius: var(--maxuiux-border-radius-xxl);
            box-shadow: var(--maxuiux-box-shadow-green);
            background-color: var(--maxuiux-color-white);

            @include to(476px) {
                align-items: center;
            }

            @media (prefers-color-scheme: dark) {
                background-color: var(--maxuiux-color-gray-1000);
            }
        }

        &__img {
            svg {
                width: var(--maxuiux-spacing-160);
                height: var(--maxuiux-spacing-160);
            }

            &--orange {
                color: var(--maxuiux-color-orange-600);
            }

            &--green {
                color: var(--maxuiux-color-green-600);
            }
        }

        &__btn {
            justify-content: center;
        }
    }
}