.approach {

    &__wrap {
        display: flex;
        flex-direction: column;
        gap: var(--maxuiux-spacing-48);
        
        @include to(1440px) { 
            gap: var(--maxuiux-spacing-44);
        }
        
        @include to(992px) { 
            gap: var(--maxuiux-spacing-36);
        }
        
        @include to(768px) { 
            gap: var(--maxuiux-spacing-26);
        }
    }

    &-head {
        &__wrap {
            max-width: 100%;
            width: 100%;
            border-radius: var(--maxuiux-border-radius-large);
            overflow: hidden;
            position: relative;
        }

        &__video {
            width: 100%;
        }

        &__info {
            position: absolute;
            top: 50%;
            left: 50%;
            z-index: var(--maxuiux-z-index-10);
            transform: translate(-50%, -50%);

            &-wrap {
                text-align: center;
                font-size: var(--maxuiux-font-size-h2);
                line-height: var(--maxuiux-line-height-h2);
                font-weight: var(--maxuiux-font-weight-bold);
                text-transform: uppercase;
                color: var(--maxuiux-color-white);
                cursor: default;
                padding: var(--maxuiux-spacing-80) var(--maxuiux-spacing-112);
                background-color: var(--maxuiux-color-black-rgba-030);
                backdrop-filter: var(--maxuiux-blur-large);
                border-radius: var(--maxuiux-border-radius-medium);
                transform: scale(1);
                transition: var(--maxuiux-transition-all-20s);

                &:hover {
                    transform: scale(1.04);
                }
                
                @media(hover: hover) {
                    &:hover {
                        transform: scale(1.04);
                    }
                }
        
                @media(hover: none) {
                    &:hover {
                        transform: scale(1.04);
                    }
                }

                @include to(1440px) {
                    font-size: var(--maxuiux-font-size-h3);
                    line-height: var(--maxuiux-line-height-h3);
                    padding: var(--maxuiux-spacing-72) var(--maxuiux-spacing-96);
                }

                @include to(1200px) {
                    font-size: var(--maxuiux-font-size-h4);
                    line-height: var(--maxuiux-line-height-h4);
                    padding: var(--maxuiux-spacing-64) var(--maxuiux-spacing-80);
                }

                @include to(992px) {
                    font-size: var(--maxuiux-font-size-h5);
                    line-height: var(--maxuiux-line-height-h5);
                    padding: var(--maxuiux-spacing-60) var(--maxuiux-spacing-72);
                }

                @include to(768px) {
                    font-size: var(--maxuiux-font-size-h6);
                    line-height: var(--maxuiux-line-height-h6);
                    padding: var(--maxuiux-spacing-52) var(--maxuiux-spacing-64);
                }

                @include to(576px) {
                    padding: var(--maxuiux-spacing-40) var(--maxuiux-spacing-56);
                }
            }
        }
    }

    &__blocks {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: var(--maxuiux-spacing-44);
        
        @include to(1440px) { 
            gap: var(--maxuiux-spacing-40);
        }
        
        @include to(1200px) { 
            grid-template-columns: repeat(2, 1fr);
        }
        
        @include to(992px) { 
            gap: var(--maxuiux-spacing-36);
        }
        
        @include to(768px) { 
            gap: var(--maxuiux-spacing-26);
        }
        
        @include to(576px) { 
            grid-template-columns: repeat(1, 1fr);
        }
    }

    &__block {
        border-radius: var(--maxuiux-border-radius-large);
        background-color: var(--maxuiux-color-gray-200);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 0%;
        overflow: hidden;
        transition: var(--maxuiux-transition-all-30s);

        @media (prefers-color-scheme: dark) {
            background-color: var(--maxuiux-color-gray-1000);
        }

        &-wrap {
            display: flex;
            flex-direction: column;
            width: 100%;
            height: 100%;
            padding: var(--maxuiux-spacing-24);
            background-color: var(--maxuiux-color-white-rgba-000);
            backdrop-filter: var(--maxuiux-blur-large);
            transition: var(--maxuiux-transition-all-30s);

            @media (prefers-color-scheme: dark) {
                background-color: var(--maxuiux-color-black-rgba-000);
            }
        }

        &:hover {
            background-size: 130%;
            background-color: var(--maxuiux-color-gray-300);

            @media (prefers-color-scheme: dark) {
                background-color: var(--maxuiux-color-gray-900);
            }

            .approach__block-wrap {
                background-color: var(--maxuiux-color-white-rgba-035);

                @media (prefers-color-scheme: dark) {
                    background-color: var(--maxuiux-color-black-rgba-025);
                }
            }
        }


        &-img {
            margin-left: auto;
            margin-right: auto;
            margin-bottom: var(--maxuiux-spacing-32);
            user-select: none;

            img {
                height: 200px;
            }
        }

        &-title {
            font-size: var(--maxuiux-spacing-20);
            font-weight: var(--maxuiux-font-weight-extra-bold);
            margin-bottom: var(--maxuiux-spacing-12);
        }

        &-text {
            font-size: var(--maxuiux-spacing-16);
            line-height: var(--maxuiux-spacing-20);
        }

        &-title,
        &-text {
            cursor: default;
        }

        &--1 {
            background-image: url(../img/approach-block-img-1.jpg);
            background-image: image-set(url(../img/approach-block-img-1.webp) type("image/webp"));
        }

        &--2 {
            background-image: url(../img/approach-block-img-2.jpg);
            background-image: image-set(url(../img/approach-block-img-2.webp) type("image/webp"));
        }

        &--3 {
            background-image: url(../img/approach-block-img-3.jpg);
            background-image: image-set(url(../img/approach-block-img-3.webp) type("image/webp"));
        }

        &--4 {
            background-image: url(../img/approach-block-img-4.jpg);
            background-image: image-set(url(../img/approach-block-img-4.webp) type("image/webp"));
        }
    }
}