.accordion {

    &__items {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-auto-rows: max-content;
        gap: var(--maxuiux-spacing-26);

        @include to(1200px) {
            grid-template-columns: repeat(2, 1fr);
        }

        @include to(768px) {
            grid-template-columns: repeat(1, 1fr);
        }
    }

    &__item {
        &-head {
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-direction: row;
            min-height: rem(117);
            gap: var(--maxuiux-spacing-26);
            font-size: var(--maxuiux-font-size-h5);
            font-weight: var(--maxuiux-font-weight-bold);
            cursor: pointer;
            background-color: var(--maxuiux-color-gray-200);
            padding: var(--maxuiux-spacing-24) var(--maxuiux-spacing-26);
            border-top-left-radius: var(--maxuiux-border-radius-large);
            border-top-right-radius: var(--maxuiux-border-radius-large);
            border-bottom-left-radius: var(--maxuiux-border-radius-large);
            border-bottom-right-radius: var(--maxuiux-border-radius-large);
            position: sticky;
            z-index: var(--maxuiux-z-index-20);
            overflow: hidden;
            transition: var(--maxuiux-transition-all-30s);

            @include to(768px) {
                min-height: initial;
                font-size: var(--maxuiux-font-size-h6);
                font-weight: var(--maxuiux-font-weight-semi-bold);
                padding: var(--maxuiux-spacing-16) var(--maxuiux-spacing-20);
            }

            &-img {
                flex-shrink: 0;
                width: var(--maxuiux-spacing-40);
                height: var(--maxuiux-spacing-40);
                transform: scale(1);
                user-select: none;
                transition: var(--maxuiux-transition-all-30s);

                @include to(768px) {
                    width: var(--maxuiux-spacing-30);
                    height: var(--maxuiux-spacing-30);
                }
            }

            @media (prefers-color-scheme: dark) {
                background-color: var(--maxuiux-color-gray-1000);
            }

            &:hover {
                background-color: var(--maxuiux-color-gray-300);

                @media (prefers-color-scheme: dark) {
                    background-color: var(--maxuiux-color-gray-900);
                }
            }

            &.active {
                // height: auto;
                border-bottom-left-radius: var(--maxuiux-spacing-0);
                border-bottom-right-radius: var(--maxuiux-spacing-0);

                .accordion__item-head-img {
                    transform: scale(1.5);
                }
            }
        }

        &-text {
            display: flex;
            flex-direction: column;
            gap: var(--maxuiux-spacing-24);
            max-height: 0;
            font-size: var(--maxuiux-font-size-h6);
            line-height: var(--maxuiux-line-height-medium);
            border: var(--maxuiux-border-width-small) solid transparent;
            border-bottom-left-radius: var(--maxuiux-border-radius-large);
            border-bottom-right-radius: var(--maxuiux-border-radius-large);
            padding: var(--maxuiux-spacing-24);
            color: transparent;
            margin-top: -50px;
            pointer-events: none;
            position: sticky;
            z-index: var(--maxuiux-z-index-10);
            overflow: hidden;
            transition: all 0.3s ease-in-out;

            @include to(768px) {
                padding: var(--maxuiux-spacing-16) var(--maxuiux-spacing-20);
            }

            &.active {
                border-color: var(--maxuiux-color-gray-200);
                margin-top: 0;
                color: inherit;

                @media (prefers-color-scheme: dark) {
                    border-color: var(--maxuiux-color-gray-1000);
                }
            }
        }
    }

}