.video {
    background-color: var(--maxuiux-color-green-100);
    padding-bottom: var(--maxuiux-spacing-80);

    @media (prefers-color-scheme: dark) {
        background-color: var(--maxuiux-color-green-1100);
    }

    @include to(1366px) {
        padding-bottom: var(--maxuiux-spacing-60);
    }
    
    @include to(992px) {
        padding-bottom: var(--maxuiux-spacing-56);
    }
    
    @include to(768px) { 
        padding-bottom: var(--maxuiux-spacing-52);
    }
    
    @include to(690px) { 
        padding-bottom: var(--maxuiux-spacing-40);
    }
    
    @include to(375px) { 
        padding-bottom: var(--maxuiux-spacing-26);
    }

    &__player {
        max-width: 100%;
        width: 100%;
        border-radius: var(--maxuiux-border-radius-large);
        overflow: hidden;
    }

}