.stack {
    &__items {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: var(--maxuiux-spacing-28) var(--maxuiux-spacing-24);

        @media (prefers-color-scheme: dark) {
            position: relative;

            &:before {
                content: "";
                position: absolute;
                z-index: var(--maxuiux-z-index-m-1);
                top: var(--maxuiux-spacing-0);
                right: var(--maxuiux-spacing-0);
                bottom: var(--maxuiux-spacing-0);
                left: var(--maxuiux-spacing-0);
                background: linear-gradient(0deg, var(--maxuiux-color-blue-600) 0%, var(--maxuiux-color-green-600) 90%);
                transform: translate3d(-10px, 0, 0) scale(1.01);
                filter: blur(100px);
                opacity: 0.5;
                transition: opacity 0.3s;
                border-radius: inherit;
            }
        }

        @include to(1200px) {
            gap: var(--maxuiux-spacing-26) var(--maxuiux-spacing-20);
        }

        @include to(992px) {
            gap: var(--maxuiux-spacing-20) var(--maxuiux-spacing-16);
        }

        @include to(576px) {
            gap: var(--maxuiux-spacing-16) var(--maxuiux-spacing-16);
        }
    }

    &__item {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: var(--maxuiux-spacing-16);
        border-radius: var(--maxuiux-border-radius-large);
        background-color: var(--maxuiux-color-gray-200);
        padding: var(--maxuiux-spacing-20) var(--maxuiux-spacing-26);
        cursor: default;
        overflow: hidden;
        transition: var(--maxuiux-transition-all-20s);

        @media (prefers-color-scheme: dark) {
            background-color: var(--maxuiux-color-black-rgba-080);
            backdrop-filter: var(--maxuiux-blur-small);

            &:hover {
                background-color: var(--maxuiux-color-black-rgba-060);
            }
            
            @media(hover: hover) {
                &:hover {
                    background-color: var(--maxuiux-color-black-rgba-060);
                }
            }
    
            @media(hover: none) {
                &:hover {
                    background-color: var(--maxuiux-color-black-rgba-060);
                }
            }
        }

        svg {
            width: var(--maxuiux-spacing-40);
            height: var(--maxuiux-spacing-40);
            transition: var(--maxuiux-transition-all-20s);
        }

        &-title {
            font-size: var(--maxuiux-font-size-h2);
            font-weight: var(--maxuiux-font-weight-bold);
            transition: var(--maxuiux-transition-all-20s);
        }

        &:hover {
            background-color: var(--maxuiux-color-gray-300);
        }
        
        @media(hover: hover) {
            &:hover {
                background-color: var(--maxuiux-color-gray-300);
            }
        }

        @media(hover: none) {
            &:hover {
                background-color: var(--maxuiux-color-gray-300);
            }
        }

        @include to(1440px) {
            padding: var(--maxuiux-spacing-16) var(--maxuiux-spacing-26);

            &-title {
                font-size: var(--maxuiux-font-size-h3);
            }

            svg {
                width: var(--maxuiux-spacing-30);
                height: var(--maxuiux-spacing-30);
            }
        }

        @include to(1200px) {
            &-title {
                font-size: var(--maxuiux-font-size-h4);
            }

            svg {
                width: var(--maxuiux-spacing-26);
                height: var(--maxuiux-spacing-26);
            }
        }

        @include to(992px) {
            &-title {
                font-size: var(--maxuiux-font-size-h5);
            }

            svg {
                width: var(--maxuiux-spacing-24);
                height: var(--maxuiux-spacing-24);
            }
        }

        @include to(768px) {
            padding: var(--maxuiux-spacing-16) var(--maxuiux-spacing-24);

            &-title {
                font-size: var(--maxuiux-font-size-h6);
            }

            svg {
                width: var(--maxuiux-spacing-20);
                height: var(--maxuiux-spacing-20);
            }
        }

        @include to(576px) {
            padding: var(--maxuiux-spacing-14) var(--maxuiux-spacing-20);
        }
    }
}