.contact {

    &__blocks {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: var(--maxuiux-color-gray-200);
        border-radius: var(--maxuiux-border-radius-large);
        padding: var(--maxuiux-spacing-6);

        @media (prefers-color-scheme: dark) {
            background-color: var(--maxuiux-color-gray-1000);
        }

        @include to(576px) {
            flex-direction: column;
            gap: var(--maxuiux-spacing-16);
        }
    }

    &__block {
        display: flex;
        align-items: center;
        flex-direction: row;
        gap: var(--maxuiux-spacing-2);

        @include to(576px) {
            flex-wrap: wrap;
            justify-content: center;
        }

        &:first-child {
            gap: var(--maxuiux-spacing-14);
        }

        &-title {
            font-size: var(--maxuiux-font-size-h5);
            font-weight: var(--maxuiux-font-weight-semi-bold);
            padding-left: var(--maxuiux-spacing-12);

            @include to(768px) {
                font-size: var(--maxuiux-font-size-h6);
            }
        }

        &-img {
            width: var(--maxuiux-spacing-40);
            height: var(--maxuiux-spacing-40);

            @include to(768px) {
                width: var(--maxuiux-spacing-30);
                height: var(--maxuiux-spacing-30);
            }
        }

        &-btn {
            gap: var(--maxuiux-spacing-8);

            svg {
                width: var(--maxuiux-spacing-24);
                height: var(--maxuiux-spacing-24);
            }

            &:hover {
                background-color: var(--maxuiux-color-white);
            }

            @media (hover: hover) {
                &:hover {
                    background-color: var(--maxuiux-color-white);
                }
            }

            @media (hover: none) {
                &:hover {
                    background-color: var(--maxuiux-color-white);
                }
            }
        }
    }

}