.page-preview {

    &__blocks {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: var(--maxuiux-spacing-36);

        @include to(768px) {
            grid-template-columns: repeat(1, 1fr);
            gap: var(--maxuiux-spacing-20);
        }
    }

    &__block {
        position: relative;

        &:first-child {
            grid-column: auto/span 1;
        }

        &:last-child {
            grid-column: auto/span 2;
            
            @include to(768px) {
                grid-column: auto/span 1;
            }
        }

        &-list {
            @include to(768px) {
                display: flex;
                flex-wrap: wrap;
                grid-template-columns: initial;
            }

            &-item {
                counter-increment: item-counter;
                position: relative;

                &::before {
                    content: counter(item-counter);
                    font-size: var(--maxuiux-font-size-h5);
                    line-height: var(--maxuiux-line-height-h5);
                    font-weight: var(--maxuiux-font-weight-bold);
                    color: var(--maxuiux-color-gray-200);
                    position: absolute;
                    top: var(--maxuiux-spacing-0);
                    left: var(--maxuiux-spacing-0);
                    z-index: var(--maxuiux-z-index-10);
                    transition: var(--maxuiux-transition-all-15s);

                    @media (prefers-color-scheme: dark) {
                        color: var(--maxuiux-color-gray-900);
                    }

                    @include to(1194px) {
                        font-size: var(--maxuiux-font-size-h6);
                        line-height: var(--maxuiux-line-height-h6);
                        position: static;
                        top: initial;
                        left: initial;
                        z-index: initial;
                    }

                }

                @include to(1194px) {
                    display: flex;
                    flex-direction: row;
                    gap: var(--maxuiux-spacing-8);
                    padding: var(--maxuiux-spacing-8) var(--maxuiux-spacing-12);
                    border-radius: var(--maxuiux-border-radius-medium);

                    &.active {
                        background-color: var(--maxuiux-color-green-100);

                        @media (prefers-color-scheme: dark) {
                            background-color: var(--maxuiux-color-green-1000);
                        }
                    }
                }

                &.active {
                    .page-preview__block-list-link {
                        span {
                            color: var(--maxuiux-color-green-600);
                            border-color: var(--maxuiux-color-green-600);
                        }
                    }

                    &::before {
                        color: var(--maxuiux-color-green-600);
                    }
                }
            }

            &-link {
                display: block;
                padding-top: var(--maxuiux-spacing-16);
                padding-left: var(--maxuiux-spacing-16);
                padding-bottom: var(--maxuiux-spacing-16);

                @include to(1194px) {
                    pointer-events: none;
                    padding-top: initial;
                    padding-left: initial;
                    padding-bottom: initial;
                }

                span {
                    font-size: var(--maxuiux-font-size-h3);
                    line-height: var(--maxuiux-line-height-h3);
                    font-weight: var(--maxuiux-font-weight-semi-bold);
                    border-bottom: var(--maxuiux-spacing-2) solid var(--maxuiux-color-gray-200);
                    transition: var(--maxuiux-transition-all-15s);

                    @media (prefers-color-scheme: dark) {
                        border-color: var(--maxuiux-color-gray-900);
                    }

                    @include to(1194px) {
                        font-size: var(--maxuiux-font-size-h4);
                        line-height: var(--maxuiux-line-height-h4);
                        border-bottom: none;
                    }

                    @include to(992px) {
                        font-size: var(--maxuiux-font-size-h5);
                        line-height: var(--maxuiux-line-height-h5);
                    }

                    @include to(768px) {
                        font-size: var(--maxuiux-font-size-h6);
                        line-height: var(--maxuiux-line-height-h6);
                    }
                }
            }
        }

        &-wrap {
            display: flex;
            flex-direction: column;
            gap: var(--maxuiux-spacing-24);
            position: sticky;
            top: var(--maxuiux-spacing-80);
            z-index: var(--maxuiux-z-index-10);

            @include to(768px) {
                gap: var(--maxuiux-spacing-20);
            }
        }

        &-img {
            border: var(--maxuiux-border-width-small) solid var(--maxuiux-color-gray-200);
            border-radius: var(--maxuiux-border-radius-medium);
            overflow: hidden;

            @media (prefers-color-scheme: dark) {
                border-color: var(--maxuiux-color-gray-1100);
            }
        }

        &-link {
            display: none;
            justify-content: space-between;
            background-color: var(--maxuiux-color-black-rgba-005);

            @media (prefers-color-scheme: dark) {
                background-color: var(--maxuiux-color-white-rgba-010);
            }

            @include to(1194px) {
                display: flex;
            }

            svg {
                width: var(--maxuiux-spacing-24);
                height: var(--maxuiux-spacing-24);
            }
        }
    }
}