.portfolio-category-preview {
    background-color: var(--maxuiux-color-gray-100);
    
    @media (prefers-color-scheme: dark) {
        background-color: var(--maxuiux-color-gray-1100);
    }

    &__title {
        font-size: var(--maxuiux-font-size-h1);
        font-weight: var(--maxuiux-font-weight-medium);
        margin-bottom: var(--maxuiux-spacing-64);

        @include to(768px) {
            font-size: var(--maxuiux-font-size-h2);
            margin-bottom: var(--maxuiux-spacing-48);
        }
    }

    &__blocks {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: var(--maxuiux-spacing-64);

        @include to(1440px) {
            gap: var(--maxuiux-spacing-44);
        }

        @include to(1200px) {
            grid-template-columns: repeat(2, 1fr);
            gap: var(--maxuiux-spacing-44) var(--maxuiux-spacing-36);
        }

        @include to(768px) {
            grid-template-columns: repeat(1, 1fr);
            gap: var(--maxuiux-spacing-40);
        }
    }

    &__block {
        grid-column: auto/span 1;

        &-title {
            display: inline-block;
            font-size: var(--maxuiux-font-size-h5);
            line-height: var(--maxuiux-line-height-large);
            border-bottom: var(--maxuiux-border-width-small) solid var(--maxuiux-color-gray-300);
            margin-bottom: var(--maxuiux-spacing-20);
            transition: var(--maxuiux-transition-all-20s);

            &:hover {
                color: var(--maxuiux-color-green-600);
                border-color: var(--maxuiux-color-green-600);
            }
        }

        &-items {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: var(--maxuiux-spacing-2);
            border-radius: var(--maxuiux-border-radius-medium);
            overflow: hidden;
            transform: scale(1);
            transition: var(--maxuiux-transition-all-20s);

            &:hover {
                transform: scale(1.03);
            }
        }

        &-item {
            grid-column: auto/span 1;
            overflow: hidden;

            &-img {
                height: 100%;
                width: 100%;
                transform: scale(1);
                transition: var(--maxuiux-transition-all-20s);

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            &:hover {
                .portfolio-category-preview__block-item-img {
                    transform: scale(1.03);
                }
            }
        }
    }
}