.animline {
    overflow: hidden;
    padding: var(--maxuiux-spacing-24) var(--maxuiux-spacing-0);
    background-color: var(--maxuiux-color-black);
    color: var(--maxuiux-color-white);

    @media (prefers-color-scheme: dark) {
        background-color: var(--maxuiux-color-black-rgba-030);
        backdrop-filter: var(--maxuiux-blur-large);
        position: relative;

        &:before {
            content: "";
            z-index: var(--maxuiux-z-index-m-1);
            position: absolute;
            top: var(--maxuiux-spacing-0);
            right: var(--maxuiux-spacing-0);
            bottom: var(--maxuiux-spacing-0);
            left: var(--maxuiux-spacing-0);
            background: linear-gradient(30deg, var(--maxuiux-color-blue-600) 0%, var(--maxuiux-color-green-600) 100%);
            filter: blur(60px);
            opacity: 0.45;
            transition: var(--maxuiux-transition-all-30s);
        }
    }

    @include to(1200px) {
        padding: var(--maxuiux-spacing-20) var(--maxuiux-spacing-0);
    }

    &__blocks {
        display: inline-flex;
        align-items: center;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
        animation-direction: var(--direction);
        animation-duration: var(--duration);

        svg {
            width: var(--maxuiux-spacing-64);
            height: var(--maxuiux-spacing-64);
            transition: var(--maxuiux-transition-all-20s);

            @include to(1440px) {
                width: var(--maxuiux-spacing-56);
                height: var(--maxuiux-spacing-56);
            }

            @include to(1200px) {
                width: var(--maxuiux-spacing-48);
                height: var(--maxuiux-spacing-48);
            }

            @include to(992px) {
                width: var(--maxuiux-spacing-40);
                height: var(--maxuiux-spacing-40);
            }

            @include to(768px) {
                width: var(--maxuiux-spacing-36);
                height: var(--maxuiux-spacing-36);
            }

            @include to(576px) {
                width: var(--maxuiux-spacing-32);
                height: var(--maxuiux-spacing-32);
            }
        }
    }
    
    &-animation {
        &--left {
            .animline__blocks {
                animation-name: infiniteScrollAnimationLeft;
            }

        }

        &--right {
            .animline__blocks {
                animation-name: infiniteScrollAnimationRight;
            }
        }
        
        @keyframes infiniteScrollAnimationLeft {
            0% {
                transform: translateX(0);
            }
    
            100% {
                transform: translateX(-50%);
            }
        }
    
        @keyframes infiniteScrollAnimationRight {
            0% {
                transform: translateX(-50%);
            }
    
            100% {
                transform: translateX(0);
            }
        }
    }

    &__block {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: var(--maxuiux-spacing-64);
        padding: 0 var(--maxuiux-spacing-36);

        @include to(1200px) {
            gap: var(--maxuiux-spacing-56);
            padding: 0 var(--maxuiux-spacing-28);
        }

        @include to(992px) {
            gap: var(--maxuiux-spacing-48);
            padding: 0 var(--maxuiux-spacing-24);
        }

        @include to(768px) {
            gap: var(--maxuiux-spacing-40);
            padding: 0 var(--maxuiux-spacing-20);
        }

        @include to(576px) {
            gap: var(--maxuiux-spacing-32);
            padding: 0 var(--maxuiux-spacing-16);
        }

        &-text {
            display: inline-block;
            user-select: none;
            white-space: nowrap;
            font-size: var(--maxuiux-spacing-64);
            font-weight: var(--maxuiux-font-weight-bold);
            text-transform: uppercase;
            transition: var(--maxuiux-transition-all-20s);

            @include to(1440px) {
                font-size: var(--maxuiux-spacing-56);
            }

            @include to(1200px) {
                font-size: var(--maxuiux-spacing-48);
            }

            @include to(992px) {
                font-size: var(--maxuiux-spacing-40);
            }

            @include to(768px) {
                font-size: var(--maxuiux-spacing-36);
            }

            @include to(576px) {
                font-size: var(--maxuiux-spacing-32);
            }
        }
    }
}