.sidebar {
    display: none;
    position: fixed;
    top: var(--maxuiux-spacing-0);
    left: var(--maxuiux-spacing-0);
    right: var(--maxuiux-spacing-0);
    bottom: var(--maxuiux-spacing-0);
    background-color: var(--maxuiux-color-black-rgba-060);
    z-index: var(--maxuiux-z-index-sidebar);
    height: 100dvh;
    overflow: hidden;
    transition: var(--maxuiux-transition-all-20s);

    &__wrap {
        width: 100%;
        height: 100%;
        position: relative;
        pointer-events: none;
    }

    &__menu {
        position: absolute;
        top: var(--maxuiux-spacing-0);
        bottom: var(--maxuiux-spacing-0);
        z-index: var(--maxuiux-z-index-10);
        width: var(--maxuiux-spacing-240);
        height: 100%;
        padding-top: var(--maxuiux-spacing-24);
        padding-left: var(--maxuiux-spacing-8);
        padding-right: var(--maxuiux-spacing-8);
        padding-bottom: var(--maxuiux-spacing-28);
        background-color: var(--maxuiux-color-white);
        overflow-x: hidden;
        box-shadow: var(--maxuiux-box-shadow-medium);
        pointer-events: all;
        transition: all 0.3s cubic-bezier(0.2, 0.8, 0.2, 1);
        
        @media (prefers-color-scheme: dark) {
            background-color: var(--maxuiux-color-gray-1100);
        }

        &-list {
            display: flex;
            flex-direction: column;
            gap: var(--maxuiux-spacing-4);

            &-item {
                &.active {
                    .sidebar__menu-list-link {
                        pointer-events: none;
                        background-color: var(--maxuiux-color-gray-300);

                        @media (prefers-color-scheme: dark) {
                            background-color: var(--maxuiux-color-gray-900);
                        }

                        .icon-default {
                            display: none;
                        }

                        .icon-fill {
                            display: flex;
                        }
                    }
                }
            }

            &-link {
                gap: var(--maxuiux-spacing-16);
                font-size: var(--maxuiux-font-size-small);

                svg {
                    flex-shrink: var(--maxuiux-spacing-0);
                    width: var(--maxuiux-spacing-24);
                    height: var(--maxuiux-spacing-24);
                }

                .icon-fill {
                    display: none;
                }
            }
        }

        &-line {
            margin: var(--maxuiux-spacing-20) auto;
            width: var(--maxuiux-spacing-64);
            height: var(--maxuiux-spacing-2);
            border-radius: var(--maxuiux-border-radius-full);
            background-color: var(--maxuiux-color-gray-200);
            transition: background-color var(--maxuiux-transition-20s);
            
            @media (prefers-color-scheme: dark) {
                background-color: var(--maxuiux-color-gray-900);
            }
        }
    }

    &[data-sidebar-active="false"] {
        opacity: 0;
        pointer-events: none;

        .sidebar__menu {
            right: -240px;
        }
    }

    &[data-sidebar-active="true"] {
        opacity: 1;
        pointer-events: all;

        .sidebar__menu {
            right: 0;
        }
    }

    @include to(1366px) {
        display: block;
    }
}