.section-portfolio {

    &.portfolio-site {
        .section-portfolio__block {
            &:nth-child(1) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(4) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(6) {
                grid-column: auto / span 2;
                grid-row: auto/span 2;
            }

            @include to(992px) {

                &:nth-child(10),
                &:nth-child(11) {
                    display: none;
                }
            }
        }
    }

    &.portfolio-design {
        .section-portfolio__block {
            &:nth-child(1) {
                grid-column: auto/span 2;
                grid-row: auto/span 2;
            }

            &:nth-child(2),
            &:nth-child(3) {
                grid-row: auto/span 2;
            }
            
            &:nth-child(4),
            &:nth-child(5) {
                grid-column: auto/span 2;
                grid-row: auto/span 2;
            }
            
            &:nth-child(8) {
                grid-column: auto/span 2;
                grid-row: auto/span 2;
            }
        }
    }

    &__wrap {
        display: flex;
        flex-direction: column;
        gap: var(--maxuiux-spacing-32);

        @include to(1440px) {
            gap: var(--maxuiux-spacing-24);
        }
    }

    &__blocks {
        display: grid;
        grid-auto-flow: row dense;
        grid-template-columns: repeat(4, 1fr);
        gap: var(--maxuiux-spacing-32);

        @media (prefers-color-scheme: dark) {
            border-color: var(--maxuiux-color-gray-1100);
        }

        @include to(1440px) {
            gap: var(--maxuiux-spacing-24);
        }

        @include to(992px) {
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__block {
        grid-row: auto/span 1;
        border: var(--maxuiux-border-width-small) solid var(--maxuiux-color-gray-200);
        border-radius: var(--maxuiux-border-radius-medium);
        overflow: hidden;

        @media (prefers-color-scheme: dark) {
            border-color: var(--maxuiux-color-gray-1100);
        }

        &-img {
            height: 100%;
            width: 100%;
            transform: scale(1);
            transition: var(--maxuiux-transition-all-20s);

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &:hover {
            .section-portfolio__block-img {
                transform: scale(1.1);
            }
        }
    }

    &__more {
        &-btn {
            text-decoration: underline;
            font-size: var(--maxuiux-font-size-h5);
            padding: var(--maxuiux-spacing-24) var(--maxuiux-spacing-32);

            @include to(768px) {
                font-size: var(--maxuiux-font-size-h6);
                font-weight: var(--maxuiux-font-weight-semi-bold);
            }

            @include to(1024px) {
                justify-content: center;
            }

            &:hover {
                color: var(--maxuiux-color-green-900);
                background-color: var(--maxuiux-color-green-300);

                @media (prefers-color-scheme: dark) {
                    color: var(--maxuiux-color-white);
                    background-color: var(--maxuiux-color-green-700);
                }
            }

            @media (hover: hover) {
                &:hover {
                    color: var(--maxuiux-color-green-900);
                    background-color: var(--maxuiux-color-green-300);

                    @media (prefers-color-scheme: dark) {
                        color: var(--maxuiux-color-white);
                        background-color: var(--maxuiux-color-green-700);
                    }
                }
            }

            @media (hover: none) {
                &:hover {
                    color: var(--maxuiux-color-green-900);
                    background-color: var(--maxuiux-color-green-300);

                    @media (prefers-color-scheme: dark) {
                        color: var(--maxuiux-color-white);
                        background-color: var(--maxuiux-color-green-700);
                    }
                }
            }
        }
    }
}